.card {
  /* background: gray; */
  /* width: ; */
  display: flex;
  box-shadow: 0 0 30px 0px rgb(201, 199, 199);
  border-radius: 0.5rem;
}

.card img {
  height: 150px;
  width: 100%;
  border-radius: 0.5rem;
}

.card-info {
  padding: 1rem 1.5rem;
}

@media screen and (min-width: 600px) {
  .card {
    flex-direction: row;
  }

  .card-info {
  }

  .card img {
    width: 150px;
  }
}
