.result-card {
  margin: 3rem 0;
  box-shadow: 0 0 50px -20px gray;
  padding: 2rem;
  border-radius: 0.75rem;
}

.result-card-img {
  border-radius: 50%;
  display: block;
  margin: auto;
}

.result-btn {
  border-radius: 0;
  width: 200px;
  display: block;
  margin: auto;
}

.feedback {
  margin: 1rem auto;
  display: block;
  text-align: center;
}

.reviews {
  margin: 0 1rem;
  font-weight: 700;
  text-decoration: underline;
}

.rating {
  font-size: 1rem;
  padding: 0.25rem 0.5rem;
}
